'use strict';

const e = React.createElement;

const ProgressBar = (props) => {
  const { bgcolor, completed } = props;

  const containerStyles = {
    height: 40,
    width: '100%',
    backgroundColor: "#e0e0de",
    borderRadius: 50,
  }

  const fillerStyles = {
    height: '100%',
    width: `${completed}%`,
    backgroundColor: bgcolor,
    borderRadius: 'inherit',
    paddingRight: 16,
    display: 'flex',
    justifyContent: 'flex-end',
    alignItems: 'center',
  }

  const labelStyles = {
    padding: 5,
    color: 'white',
    fontWeight: 'bold',
  }

  return (
    <div style={containerStyles}>
      <div style={fillerStyles}>
        <span style={labelStyles}>{`${completed}%`}</span>
      </div>
    </div>
  );
};


const Processbar = () => {

  const data = [
    { bgcolor: "#5D34FF", completed: 38 },
  ];


    return (
      <div>
        {data.map((item, idx) => (
          <ProgressBar key={idx} bgcolor={item.bgcolor} completed={item.completed} />
        ))}
      </div>
      )
}

const domContainer = document.querySelector('#processbar');
ReactDOM.render(e(Processbar), domContainer);